(function() {
  'use strict';

  angular
    .module('stateAuto')
    .controller('SalesFunnelController', SalesFunnelController);

    /** @ngInject */
    function SalesFunnelController($scope, $http, drupalData) {
      var vm = this;

      vm.title = 'Sales & service';

      $scope.selectedRow = null;  // initialize our variable to null

       $scope.setClickedRow = function(index, $event){  //function that sets the value of selectedRow to current index

         if ($event.currentTarget.attributes.class.value.indexOf('selected') == -1) {


           console.log($event.currentTarget.attributes['ng-class'])
         }
          $scope.selectedRow = index;
       }
      $('.sf-option').click(function(event){
        event.preventDefault();
        console.log('clicked')
      })


      window.requestJSON = {decisionService: {}};
	     window.productIdx = 0;

       //This is where we are receiving the Json from Drupal. Take this input however required if separately trying to be plugged in.
      	// var salesfunnel_data = Drupal.settings.salesfunnel.sf_data['gain'];
        var salesfunnel_data = drupalData.drupalData['gain'];


    	$scope.questions_list = [salesfunnel_data];


    	$scope.parent_titles = ['Product Areas'];
    	$scope.link_path = null;

      $scope.buildList = function(contentTitle, nextKey, nextTitle, accord_key, $event){

         var idx = parseInt($event.currentTarget.closest('.questions').id.split('_')[1]);

           //To call the correct parent product, we need to keep the parent product index updated
         if(accord_key != undefined){
          window.productIdx = $event.currentTarget.dataset['parentidx'];
         }

        //Code to remove the lower hierarchicial select lists, and reload the next level alone.
        if($scope.questions_list.length > idx+1)
        {
          //Delete Request JSON unwanted keys
          var keys = $scope.parent_titles.slice(idx+1, $scope.questions_list.length);
          for(var i=0; i < keys.length; i++)
          {
            delete window.requestJSON['decisionService'][keys[i]];
          }
          //Delete Request JSON unwanted keys -- end

          $scope.questions_list = $scope.questions_list.slice(0,idx+1);
          $scope.parent_titles = $scope.parent_titles.slice(0,idx+1);
        }
        //Code to remove the lower hierarchicial select lists, and reload the next level alone.--end

        if(nextKey != undefined){ //nextKey and nextTitle appears together
          $scope.parent_titles.push(nextTitle);
          $scope.questions_list.push(salesfunnel_data[window.productIdx][nextKey]);
          //Need to null out link_path if the child is an array.
          $scope.link_path = null;
        }
        else{
          $scope.link_path = '#';
        }


  //Construct Request JSON
  window.requestJSON['decisionService'][$scope.parent_titles[idx]] = contentTitle;
};

$scope.openLink = function(){
  alert(JSON.stringify(window.requestJSON));
};



    }

})();

