(function() {
  'use strict';

  angular
    .module('stateAuto')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $timeout, $mdMedia, $mdSidenav) {

    // for custom media queries with html attribute using md-media service
    $rootScope.$mdMedia = $mdMedia;

    // for sidenav hide/show
    $rootScope.toggleMenu = function() {
      $mdSidenav('left').toggle();
    }

    $log.debug('runBlock end');




  }

})();

(function() {
  'use strict';

  angular
    .module('stateAuto')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('main', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('information', {
        url: '/information',
        templateUrl: 'app/components/gpaInfoForm/gpainfoform.html',
        controller: 'InfoFormController',
        controllerAs: 'vm'
      })
      .state('sales-service', {
        url: '/sales-service',
        templateUrl: 'app/components/salesFunnel/sales-service.html',
        controller: 'SalesFunnelController',
        controllerAs: 'vm'
      })

    $urlRouterProvider.otherwise('/information');
  }

})();


